* {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: StratosSkyeng;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("./assets/stratosskyengweb-regular.woff2") format("woff2");
}

body {
  font-family: MyFont, sans-serif;
}

.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-flow: column wrap;
  background-color: #004980;
}

.difficulty-block,
.finish-game {
  width: 480px;
  height: 459px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  border-radius: 12px;
  background: #c2f5ff;
  padding-top: 26px;
  margin-top: 158px;
}

.difficulty-title {
  color: #004980;
  text-align: center;
  width: 208px;
  height: 96px;
  padding-top: 26px;
  font-size: 40px;
  font-family: StratosSkyeng;
  line-height: 48px;
}

.radio-inputs {
  display: flex;
  flex-flow: row wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  gap: 26px;
  padding: 48px 0 67px;
  border: none;
}

label {
  width: 108px;
  height: 108px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.level-button {
  width: 98px;
  height: 98px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  color: #0080c1;
  font-size: 64px;
  font-family: StratosSkyeng;
  border-radius: 12px;
  background: #fff;
  border: none;
}

.level-button:hover {
  border: 0.2px solid #44d2ff;
}

input[type="radio"] {
  display: none;
}

input[type="radio"]:checked + .level-button {
  scale: 1.15;
  border: 3px solid #44d2ff;
}

.start-button,
.restart {
  width: 246px;
  height: 48px;
  color: #fff;
  font-size: 24px;
  font-family: StratosSkyeng;
  line-height: 32px;
  border-radius: 12px;
  background: var(--salad-60, #7ac100);
  border: none;
}

.start-button:hover {
  scale: 1.03;
  border: 3px solid #44d2ff;
}

.image {
  width: 96px;
  height: 96px;
  flex-shrink: 0;
}

.finish-game {
  z-index: 1;
}

.win-title {
  color: var(--blue-120, #004980);
  font-size: 40px;
  font-family: StratosSkyeng;
  line-height: 48px;
  width: 280px;
  padding: 9px 0 28px;
}

.time-text {
  color: #000;
  font-size: 24px;
  font-family: StratosSkyeng;
  line-height: 32px;
}

.time-value {
  color: #000;
  font-size: 64px;
  font-family: StratosSkyeng;
  line-height: 72px;
  padding: 10px 0 40px;
}

.play-field {
  display: flex;
  width: 976px;
}

ul {
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

.level-6 {
  width: 320px;
}

.level-12 {
  width: 430px;
}

.level-18 {
  width: 650px;
}

.game-header {
  margin: 22px 0 35px;
  width: 976px;
  display: flex;
  flex-flow: row nowrap;
  align-content: center;
  justify-content: space-between;
  align-items: center;
}

.timer {
  font-family: StratosSkyeng;
  font-weight: 400;
  color: #fff;
  width: 155px;
  height: 91px;
}

.time {
  font-size: 64px;
  line-height: 72px;
  letter-spacing: 0;
  text-align: left;
}

.time-labels {
  font-family: StratosSkyeng;
  font-size: 16px;
  font-style: normal;
  line-height: 32px;
  padding-right: 5px;
}

.timer-title {
  width: 155px;
  display: flex;
  justify-content: flex-end;
  flex-flow: row nowrap;
  align-content: center;
  align-items: center;
  gap: 52px;
}

.shirt {
  width: 95px;
  height: 133px;
  background-image: url(".//assets/images/cards/рубашка.png");
  background-size: contain;
  background-repeat: no-repeat;
  transition: all 0.2s linear;
}

.hide {
  display: none;
}

.shirt.flip {
  transform: perspective(100px) rotateY(180deg);
}

.success {
  box-sizing: border-box;
}
